.card-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.card {
    width: 300px;
    height: 400px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.5s;
    border: 3px solid black;
    border-radius: 10px;
}

.card.flipped {
    transform: rotateY(180deg);
}

.front {
    padding-top: 30px;
}

.front,
.back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
}


.back.flipped {
    display: flex;
}

.back {
    transform: rotateY(180deg);
    /*display: flex;*/
    display: none;
    justify-content: center;
    align-items: center;
}

p {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
}